*{
    box-sizing: border-box;
}
.column-3{
    width: 75%;
    float: left;
}
.column-2{
    width: 50%;
    float: left;
}
.column-1{
    width: 25%;
    float: left;
}
button.calc-button{
    width: 100%;
    padding: 5px;
    height: 100px;
    border:2px #ddd solid;
    background: #eee;
    font-size: 32px;
    color: #555;
}
.result{
    height: 100px;
    width: 100%;
    border: none;
    text-align: left;
    font-size: 32px;
    position: relative;
    /* max-width: 100vw; */
    /* margin-right: 50px; */
    padding-left: 50px;
    width: 100%;
    float: left;
}

.floaty-last{
    position: absolute;
    width: 100%;
    padding-top: 40px;
    padding-left: 55px;
    top: -30px;
    /* left: 50px; */
    z-index: 99;
    color: gray;
}
.box{
    /* width: 500px; */
    max-width: 800px;
    margin: 0 auto;
  }